<template>
  <div class="main">
    <div class="tabs">
      <div :class="{active:tab == 2}" @click="tab = 2">生产时效图</div>
      <div :class="{active:tab == 1}" @click="tab = 1">生产时效表</div>
    </div>
      <table v-show="tab == 1">
          <tr>
              <td colspan="3">生产时效（PT）</td>
              <td colspan="3">非生产时效（NPT）</td>
          </tr>
          <tr>
              <td style="width:16%;">1</td>
              <td style="width:16%;">移井架/搬迁</td>
              <td style="width:16%;">10</td>
              <td rowspan="2" style="width:16%;">20</td>
              <td rowspan="2" style="width:16%;">油套管问题</td>
              <td rowspan="2" style="width:16%;">&nbsp;</td>
          </tr>
          <tr>
              <td>2</td>
              <td>拆/装BOP及试压</td>
              <td>&nbsp;</td>
              
          </tr>
          <tr>
              <td>3</td>
              <td>接钻具</td>
              <td>&nbsp;</td>
              <td>21</td>
              <td>井控复杂</td>
              <td>&nbsp;</td>
              
          </tr>
          <tr>
              <td>4</td>
              <td>钻套管附件和水泥塞</td>
              <td>&nbsp;</td>
              <td>22</td>
              <td>卡钻/起下钻遇阻</td>
              <td>&nbsp;</td>
              
          </tr>
          <tr>
              <td>5</td>
              <td>滑动钻进</td>
              <td>&nbsp;</td>
              <td>23</td>
              <td>落鱼处理</td>
              <td>&nbsp;</td>
          </tr>
          <tr>
              <td>6</td>
              <td>复合钻进</td>
              <td>&nbsp;</td>
              <td>24</td>
              <td>井漏</td>
              <td>&nbsp;</td>
              
          </tr>
          <tr>
              <td>7</td>
              <td>取芯</td>
              <td>&nbsp;</td>
              <td rowspan="2">25</td>
              <td rowspan="2">井眼问题（缩径/井壁垮塌）</td>
              <td rowspan="2">&nbsp;</td>
              
          </tr>
          <tr>
              <td>8</td>
              <td>通井</td>
              <td>&nbsp;</td>
              
          </tr>
          <tr>
              <td>9</td>
              <td>扩划眼</td>
              <td>&nbsp;</td>
              <td>26</td>
              <td>卡测井仪器</td>
              <td>&nbsp;</td>
              
          </tr>
           <tr>
              <td>10</td>
              <td>起下钻</td>
              <td></td>
              <td>27</td>
              <td>定向仪器故障</td>
              <td>&nbsp;</td>
              
          </tr>
           <tr>
              <td>11</td>
              <td>循环处理钻井液 </td>
              <td></td>
              <td>28</td>
              <td>固井问题</td>
              <td>&nbsp;</td>
              
          </tr>
          <tr>
              <td>12</td>
              <td>保养设备、倒大绳</td>
              <td></td>
              <td>29</td>
              <td>井口装置问题</td>
              <td>&nbsp;</td>
              
          </tr>
          <tr>
              <td>13</td>
              <td>测井</td>
              <td></td>
              <td>30</td>
              <td>修理</td>
              <td>&nbsp;</td>
              
          </tr>
          <tr>
              <td>14</td>
              <td>下套管</td>
              <td></td>
              <td>31</td>
              <td>侧钻</td>
              <td>&nbsp;</td>
              
          </tr>
          <tr>
              <td>15</td>
              <td>固井/侯凝</td>
              <td></td>
              <td>32</td>
              <td>非计划起钻</td>
              <td>&nbsp;</td>
              
          </tr>
          <tr>
              <td>16</td>
              <td>安装井口装置</td>
              <td></td>
              <td>33</td>
              <td>组织停工</td>
              <td>&nbsp;</td>
              
          </tr>
          <tr>
              <td>17</td>
              <td>拆装钻机设备</td>
              <td></td>
              <td>34</td>
              <td>等人员</td>
              <td>&nbsp;</td>
              
          </tr>
          <tr>
              <td>18</td>
              <td>甩钻具</td>
              <td></td>
              <td>35</td>
              <td>等设备</td>
              <td>&nbsp;</td>
              
          </tr>
          <tr>
              <td>19</td>
              <td>生产辅助</td>
              <td></td>
              <td>36</td>
              <td>其它</td>
              <td>&nbsp;</td>
          </tr>
          <tr>
              <td colspan="2">生产合计</td>
              <td></td>
              <td colspan="2">非生产合计</td>
              <td>&nbsp;</td>
          </tr>
      </table>
      <div v-show="tab == 2" class="map">
        <div id="line" style="height: 200px;"></div>
        <div class="flex">
            <div id="pie" style="width: 400px;height: 400px;"></div>
            <table class="table">
                <tr>
                    <th>类型</th>
                    <th>滑动钻进</th>
                    <th>复合钻进</th>
                    <th>固井</th>
                    <th>测井</th>
                    <th>生成辅助</th>
                    <th>非生产时效</th>
                </tr>
                <tr>
                    <td>时间</td>
                    <td>12</td>
                    <td>12</td>
                    <td>12</td>
                    <td>12</td>
                    <td>12</td>
                    <td>12</td>
                </tr>
                <tr>
                    <td>百分比</td>
                    <td>12</td>
                    <td>12</td>
                    <td>12</td>
                    <td>12</td>
                    <td>12</td>
                    <td>12</td>
                </tr>
            </table>
        </div>
      </div>
  </div>
</template>

<script>
import echarts from 'echarts'
import request from "@/utils/request";
export default {
  props: ["route"],
  data() {
    return {
      tab: 2,
      info: {}
    };
  },

  components: {},

  computed: {},

  mounted: function() {
    let params = this.route;
    request({
      url: "/singlewell/workcondition?well_no=test&well_hole=hole1",
      method: "get",
      params
    }).then(res => {
         console.log(res);
    });
    this.draw()
  },

  methods: {
    draw() {
       this.line();
       this.pie()
    },
    line() {
        let line = echarts.init(document.getElementById('line'));
        line.setOption({
            color: ['#3398DB'],
            tooltip : {
                trigger: 'axis',
                axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                    type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                }
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            xAxis : [
                {
                    type : 'category',
                    data : ['移井架/搬迁', '油套管问题', '拆/装BOP及试压', '接钻具', '井控复杂', '井控复杂', '钻套管附件和水泥塞','移井架/搬迁', '油套管问题', '拆/装BOP及试压', '接钻具', '井控复杂', '井控复杂', '钻套管附件和水泥塞'],
                }
            ],
            yAxis : [
                {
                    type : 'value'
                }
            ],
            dataZoom: [
                {
                    type: 'inside'
                }
            ],
            series : [
                {
                    name:'直接访问',
                    type:'bar',
                    barWidth: '60%',
                    data:[10, 52, 200, 334, 390, 330, 220,10, 52, 200, 334, 390, 330, 220]
                }
            ]
        })
    },
    pie() {
        let pie = echarts.init(document.getElementById('pie'));
        pie.setOption({
            tooltip : {
                trigger: 'item',
                formatter: "{a} <br/>{b} : {c} ({d}%)"
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            series : [
                {
                    name: '访问来源',
                    type: 'pie',
                    radius : '55%',
                    center: ['50%', '50%'],
                    data:[
                        {value:335, name:'直接访问'},
                        {value:310, name:'邮件营销'},
                        {value:234, name:'联盟广告'},
                        {value:135, name:'视频广告'},
                        {value:1548, name:'搜索引擎'}
                    ],
                    itemStyle: {
                        emphasis: {
                            shadowBlur: 10,
                            shadowOffsetX: 0,
                            shadowColor: 'rgba(0, 0, 0, 0.5)'
                        }
                    }
                }
            ]
        })
    }
  }
};
</script>

<style lang='scss' scoped>
.main {
  background: #27496adc;
  min-width: 1080px;
  width: 90%;
  height: calc(100% - 40px);
  padding: 30px;
  margin: 20px auto;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid #fff;
  border-radius: 3px;
  overflow: auto;
}
.tabs {
  display: flex;
  margin-bottom: 20px;
  div {
    margin-right: 16px;
    color: #fff;
    padding-bottom: 4px;
    cursor: pointer;
    &.active {
      border-bottom: 2px solid #fff;
    }
  }
}
table {
  width: 90%;
  height: calc(100% - 80px);
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  margin: 0 auto;
  border-top: 1px solid #fff;
  border-left: 1px solid #fff;
  border-radius: 3px;
  border-collapse: collapse;
  border-spacing: 0;
  color: #fff;
}
.map {
    height: calc(100% - 50px);
    background: #fff;
    overflow: auto;
}

table tr {
  width: 100%;
  height: 30px;
  line-height: 30px;
}
table tr td {
  text-align: center;
  font-size: 14px;
  padding: 0;
  border-right: 1px solid #fff;
  border-bottom: 1px solid #fff;
}
.table {
    color: #333;
    margin-top: 90px;
    border: 1px solid #333;
    width: calc(100% - 510px);
    td,th {
        border: 1px solid #333;
        line-height: 3;
        text-align: center;
    }
}
</style>



